.remoteBlock{
    display: flex;
    align-items: center;
    min-height: 20vh;
    background-color: #FFFEED;

}

.remoteContainer{
    margin: 5% auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.title{
    text-align: center;
    margin: 0 auto 5%;
    background-color: #D9D9D9;
}

.button{
    width:150px;
    height: 40px;
    background-color: #929292;
    color: black;
    font-weight: bold;
}
