.contactBlock{
    display: flex;
    align-items: center;
    min-height: 100vh;
    background-color: #FDE2E2;

}

.contactContainer{
    flex-direction: column;
    justify-content: flex-start;
    flex-wrap: nowrap;
}


.form{
    border: 1px solid;
    min-height: 30vh;
    min-width: 60vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

}

.input1{
    background-color: #C5C3C5;

}

.input2{

    background-color: #C5C3C5;

}

.textarea{
    background-color: #C5C3C5;

}

.title{
    text-align: center;
    margin: 0 auto 5%;
    background-color: #D9D9D9;
}

.button{
    width:150px;
    height: 40px;
    background-color: #929292;
    color: black;
    font-weight: bold;
}
