.footerBlock{
    display: flex;
    align-items: center;
    min-height: 30vh;
    background-color: #ECFFE5;

}

.footerContainer{
    min-height: 20vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.title{
    text-align: center;
    margin: 0 auto;
    background-color: #D9D9D9;
}

.boxes{
    border: 1px solid;
    min-width: 210px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.box{
    width: 30px;
    height: 30px;
    background-color: #b4adad;
}