.skillsBlock{
    display: flex;
    align-items: center;
    min-height: 100vh;
    background-color: #DDFEE9;

}


.skillsContainer{
    flex-direction: column;
    justify-content: flex-start;
    flex-wrap: nowrap;


}

.skills{
    border: 1px solid;
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: center;
    align-items: stretch;
}

.title{
    margin: 0 auto 5%;
    background-color: #D8D8D8;

}

