.worksBlock{
    display: flex;
    align-items: center;
    min-height: 100vh;
    background-color: #F1E2FF;

}

.worksContainer{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.works{
    border: 1px solid;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: stretch;
}

.title{
    margin: 0 auto 5%;
    background-color: #D8D8D8;

}