.work {

    border: 1px solid;
    min-width: 250px;
    background-color: #fff;
    padding: 10px 0px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    text-align: center;
}
.icon{
    width:250px;
    height: 250px;
    background-color: #C4C4C4;
    display: flex;
    justify-content: center;
    align-items: center;
}

.button{
    width:150px;
    height: 40px;
    background-color: #929292;
    color: black;
    font-weight: bold;
}

.title{
    background-color: #D9D9D9;
}

.description{
    background-color: #D9D9D9;
    height: 50px;
    width: 200px;
}